import React from 'react';
import styled from 'styled-components';

const Card = styled.div`
	background: rgba(236, 240, 241, 0.7);
	height: 600px;
	img {
		width: 100%;
		height: 50%;
		object-fit: contain;
	}
`;

const ProjectCard = ({ project }) => (
	<Card>
		<img src={project.image} alt="" />
		{project.name}
	</Card>
);

export default ProjectCard;
